<template>
  <!-- <wrap> -->
    <div>
      <div class="word-item" v-for="(item, index) in list" :key="index">
        <span>{{ item.a }}</span>
        <span>{{ item.b }}</span>
        <span>{{ item.c }}</span>
      </div>
    </div>
</template>
<script>
export default {
    props:{
        list:{
            type:Array,
            default(){
                return []
            }
        }
    },
};
</script>
<style lang="scss" scoped>
.word-item{
  padding:10px;
  span{
    display: inline-block;
    font-size: 14px;
    font-weight: 600;
    width: 25%;
    &:nth-child(1){
      color:$self-common-fontcolor;
    }
    &:nth-child(2){
      color:$self-checked-color;
    }
    &:nth-child(3){
      color:$self-choc;
    }
  }
  &:nth-child(2n+1){
